  padding: 0;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), transform 2.5s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 20px;
  right: 20px;
}

.mobile-menu__close-menu:hover, .mobile-menu__close-menu:focus {
